import React from 'react';
import { useSelector } from 'react-redux';
import { Loading } from 'components';
import { VIDEO, VIDEO_PREVIEW_ERROR } from '../../UploadConstants';
import { PreviewError, VideoElement, VideoPreview } from './styles';


const MoviePreview = () => {

  const { poster, isLoading, videoPreview, previewError } = useSelector(({ moderatorReducer }) => ({
    poster: moderatorReducer.movieData.Poster,
    isLoading: moderatorReducer.movieData.isLoading,
    videoPreview: moderatorReducer.uploadInfo[VIDEO],
    previewError: moderatorReducer.uploadInfo[VIDEO_PREVIEW_ERROR],
  }));

  return (
    <VideoPreview>
      {isLoading && <Loading />}
      {previewError && <PreviewError elevation={!!poster ? 8 : 0}>{previewError}</PreviewError>}
      <VideoElement
        // poster='data:image/jpeg;base64,'
        poster={poster}
        controls={!!videoPreview && !previewError}
      >
        {videoPreview && !previewError &&
          <source src={videoPreview}/>
        }
      </VideoElement>
    </VideoPreview>
  );
};

export default MoviePreview;
