import React from 'react';
import { Chip } from 'components/basic';

const DataChip = ({ onClose, text }) => {

  function handleClose() {
    onClose(text)
  }

  return (
    <Chip
      leadingIcon={{
        src: 'data:image/jpeg;base64,',
        // src: 'data:image/jpeg;base64,',
        alt: 'User photo'
      }}
      color='secondary'
      chipText={text}
      onClose={handleClose}
    />
  );
};

export default DataChip;
